import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'
import { createStyles, Theme, withStyles, WithStyles } from '@material-ui/core/styles'
// import AddIcon from '@material-ui/icons/Add';
import Typography from '@material-ui/core/Typography'
import React from 'react'
import EnhancedTable from '../../components/Enhanced/Table/Table'
import Mutation from '../../components/Mutation'
import Query from '../../components/Query'
import TableWrapper from '../../components/TableWrapper'
import { SUPPLIER_TRANSACTION_TABLE } from '../../graphql/mutation/local'
import { SUPPLIER_TRANSACTION_TABLE as SUPPLIER_TRANSACTION_TABLE_QUERY } from '../../graphql/query/local'
import { STOCK_SUPPLIER, SUPPLIER_TRANSACTIONS } from '../../graphql/query/remote'
import { defaultState } from '../../graphql/resolvers'
import { Routes } from '../../Router'
import commanRender, { RENDER_VALUE } from '../../utils/commanRender'
import getContactJSX from '../../utils/getContactJSX'
import toCurrency from '../../utils/toCurrency'

const styles = (theme: Theme) => createStyles({
  paper: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing.unit * 2,
    paddingBottom: theme.spacing.unit * 2,
    marginTop: theme.spacing.unit * 2,
    marginBottom: theme.spacing.unit * 2,
  },
  fab: {
    position: 'fixed',
    bottom: theme.spacing.unit * 2,
    right: theme.spacing.unit * 2,
  },
})

const columnData = [
  { id: 'createdAt', numeric: false, disablePadding: false, colSpan: 1, label: 'Created At' },
  { id: 'quantity', numeric: false, disablePadding: false, colSpan: 1, label: 'Quantity' },
  { id: 'type', numeric: false, disablePadding: false, colSpan: 1, label: 'Type' },
  { id: 'stock', numeric: false, disablePadding: false, colSpan: 1, label: 'Stock' },
]
const exclude = ['id', 'count', 'createdAt', 'updatedAt', '__typename']

interface ISupplierDetailProps extends WithStyles<typeof styles> {
  history?: any,
  match?: any,
  user: any
}

const SupplierDetail = withStyles(styles)((props: ISupplierDetailProps) => {
  const { id } = props.match.params
  const { classes, history, user } = props
  const { supplierTransactionTable } = defaultState
  return (
    <React.Fragment>
      <Query query={STOCK_SUPPLIER} variables={{ id }}>
        {({ data: { supplier } }: IGraphqlData) => {
          return (
            <React.Fragment>
              <Paper className={classes.paper}>
                <Grid container={true} spacing={32}>
                  <Grid item={true} container={true} direction='column' sm={4}>
                    <Grid item={true}>
                      <Typography variant='headline' className='ttc' color='primary'>
                        Name: {supplier.name}
                      </Typography>
                    </Grid>
                    <Grid item={true}>
                      <Typography variant='headline' color='secondary'>
                        contact: {supplier.contact}
                      </Typography>
                    </Grid>
                  </Grid>
                  {/* <Grid item container direction="column" sm={4}>
                    <Grid item>
                      <Typography variant="subheading">
                        Available: {toCurrency(supplier.available)} {supplier.unit}
                      </Typography>
                    </Grid>
                  </Grid> */}
                </Grid>
              </Paper>

              <TableWrapper
                tableMutation={SUPPLIER_TRANSACTION_TABLE}
                tableQuery={SUPPLIER_TRANSACTION_TABLE_QUERY}
                dataQuery={SUPPLIER_TRANSACTIONS}
                getQueryVariables={() => ({ id })}
                defaultState={supplierTransactionTable}
              >
                {(tableSettings: typeof supplierTransactionTable, lmutate: Function, { loading, data, refetch }: IGraphqlData) => {
                  const stock = data[Object.keys(data)[0]]
                  const supplierTransactions = stock.transactions
                  return (
                    <EnhancedTable
                      loading={loading}
                      data={supplierTransactions.edges}
                      count={supplierTransactions.totalCount}
                      settings={supplierTransactionTable}
                      title='Stock Item Transactions'
                      columnData={columnData}
                      exclude={exclude}
                      // options={{
                      //   onEdit: (user && user.role === "1") ? (async (args: any) => {
                      //     history.push(Routes.StockTransaction.create({ supplierId: id, edit: true, id: args }));
                      //   }) : (undefined),
                      //   onAdd: () => history.push(Routes.StockTransaction.create({ supplierId: id, edit: false, id: undefined }))
                      // }}
                      render={commanRender({
                        match: 'type',
                        alt: true,
                        altValue: 'DEBIT',
                        altColor: '#eee',
                        renderValue: ({ key, value }: any) => {
                          if (key === 'contact') {
                            return { value: getContactJSX(value) }
                          }
                          const result = RENDER_VALUE({ key, value, skip: ['contact'] })
                          return { ...result, value: String(value).length ? result.value : '-' }
                        },
                      })}
                      mutate={async args => {
                        await lmutate({ variables: args })
                        // refetch();
                      }}
                    >
                    </EnhancedTable>
                  )
                }}
              </TableWrapper>
            </React.Fragment>
          )
        }}
      </Query>
    </React.Fragment>
  )
})

export default SupplierDetail
